@import url(https://fonts.googleapis.com/css2?family=EB+Garamond:wght@800&family=Roboto:wght@400;700&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

body {
  background-image: url(/suggest-a-book/static/media/booksBackground.1fe2af0a.jpg);
  background-repeat: repeat;
  font-family: 'Roboto', sans-serif;
  font-weight: 400;
  font-size: 20px;
}

.App {
  text-align: center;
}

h1 {
  font-family: 'EB Garamond', serif;
  font-weight: 800;
  font-size: 80px;
  margin: 40px auto;
}

.genre {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

form {
  width: 70%;
}

fieldset {
  background-color: #FFEDC7;
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
}

fieldset legend {
  font-weight: 700;
}

fieldset label {
  width: 20%;
  padding: 10px;
  text-align: left;
  margin: 5px;
}

fieldset input {
  margin-right: 8px;
}

form button {
  background-color: #214B87;
  color: #FFFFFF;
  font-family: 'Roboto', sans-serif;
  font-weight: 700;
  font-size: 20px;
  border: none;
  padding: 20px 40px;
  margin: 40px;
  border-radius: 40px;
}

img {
  width: 30%;
}

.bookSection {
  display: flex;
  justify-content: center;
  padding-bottom: 60px;
}

.bookReturn {
  background-color: #FFEDC7;
  display: flex;
  justify-content: center;
  align-items: center;
  width: 50%;
}

.bookDetails {
  width: 40%;
  margin: auto 5%;
  font-size: 24px;
}

.title {
  font-weight: 700;
  margin: 4px auto;
}

.subtitle {
  margin: 0 auto 20px auto;
}

.author p {
  margin: 4px auto;
}

